import axios from 'axios'
import Qs from 'qs'

// const path = 'http://192.168.1.142:3001'
const path = 'http://117.50.43.204:8000/admin/v1/'
const stuPath = 'http://117.50.43.204:8000/stu/v1/'
module.exports = {
  _get (url, data, success, fail) {
    axios({ method: 'get', url: `${path}${url}`, params: data }).then((res) => {
      success(res)
    }).catch((res) => {
      alert(res.response.data.msg)
      fail(res)
    })
  },
  _post (that, url, data, success, fail) {
    axios({
      method: 'post',
      url: `${path}${url}`,
      // data: Qs.stringify(data),
      data: Qs.stringify(data)
      // headers: {
      //   'Content-Type': 'application/x-www-form-urlencoded'
      // }
    }).then((res) => {
      // console.log(res)
      if (res.data.error_code !== 0) {
        alert(res.data.msg)
      } else {
        success(res)
        if (url.slice(0, 25) !== 'admin/geshui/company/list' && data && res.data.msg !== 'success') {
          that.$message.success(res.data.msg)
        }
      }
    }
    ).catch((res) => {
      console.log(res)
      if (res.response.data.msg) {
        alert(res.response.data.msg)
      }
      fail(res)
    })
  },
  _delete (that, url, data, success, fail) {
    axios({
      method: 'delete',
      url: `${path}${url}`,
      data: Qs.stringify(data)
    }).then((res) => {
      console.log(res)
      if (res.data.error_code !== 0) {
        alert(res.data.msg)
      } else {
        success(res)
        if (url.slice(0, 21) !== 'admin/kp/company/list' && data && res.data.msg !== 'success') {
          that.$message.success(res.data.msg)
        }
      }
    }
    ).catch((res) => {
    })
  },
  _getStu (url, data, success, fail) {
    axios({ method: 'get', url: `${stuPath}${url}`, params: data }).then((res) => {
      success(res)
    }).catch((res) => {
      alert(res.response.data.msg)
      fail(res)
    })
  },
  _postStu (that, url, data, success, fail) {
    axios({
      method: 'post',
      url: `${stuPath}${url}`,
      // data: Qs.stringify(data),
      data: Qs.stringify(data)
      // headers: {
      //   'Content-Type': 'application/x-www-form-urlencoded'
      // }
    }).then((res) => {
      // console.log(res)
      if (res.data.error_code !== 0) {
        alert(res.data.msg)
      } else {
        success(res)
        if (url.slice(0, 25) !== 'admin/geshui/company/list' && data && res.data.msg !== 'success') {
          that.$message.success(res.data.msg)
        }
      }
    }
    ).catch((res) => {
      alert(res.response.data.msg)
      fail(res)
    })
  }
}



